(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .service('settingsStore', settingsStore);

  /** @ngInject */
  function settingsStore($q, $window, api) {
    var settings = null;

    var service = {
      load: load,
      save: save
    }
    return service;

    function save(value) {
      return api.put('settings', value);
    }

    function load(defaultSettings) {
      if (settings) {
        // If we've already loaded the settings once, just give that.
        return $q.resolve(settings);
      } else {
        return api.get('settings').then(function(resp) {
          // If the loaded settings lack any of the settings attributes, we will
          // get them from the default settings.
          settings = angular.extend({}, defaultSettings, resp.data);
          parseDates(settings.workStartTimes);
          parseDates(settings.workEndTimes);
          return settings;
        });
      }
    }

    function parseDates(values) {
      for (var i = 0; i < values.length; i++) {
        values[i] = new Date(values[i]);
      }
    }
  }
})();

(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .controller('SettingsController', SettingsController);

  /** @ngInject */
  function SettingsController(GApi, $log, settingsStore, toastr) {
    var vm = this;
    vm.calendars = [];
    vm.weekDays = ['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'];
    var defaultSettings = {
      appointmentsCalId: null, tasksCalId: null,

      // Default work times are 9am-5pm Mon-Fri and just an empty 9am-9am for
      // Sun (first day of week) and Sat (last day of week).
      workStartTimes: Array(7).fill(new Date(0, 0, 0, 9)),
      workEndTimes: [new Date(0, 0, 0, 9)].concat(
        Array(5).fill(new Date(0, 0, 0, 17))).concat(
        [new Date(0, 0, 0, 9)])
    }

    vm.settings = {};
    vm.saveSettings = saveSettings;

    activate();

    function activate() {
      loadCalendars();
      loadSettings();
    }

    function loadSettings() {
      settingsStore.load(defaultSettings).then(function(loadedSettings) {
        vm.settings = loadedSettings;
      });
    }

    function saveSettings() {
      settingsStore.save(vm.settings).then(function() {
        toastr.success('Settings saved!');
      });
    }

    function loadCalendars() {
      GApi.request({
        path: '/calendar/v3/users/me/calendarList'
      }).then(
        function(resp) {
          vm.calendars = resp.result.items;
        },
        function() {
          vm.calendars = [];
          $log.debug('Calendar call failed!');
        }
      );
    }
  }
})();

/*global angular */

/**
 * Services that persists and retrieves todos from localStorage or a backend API
 * if available.
 *
 * They both follow the same API, returning promises for all changes to the
 * model.
 */
angular.module('wizweekPy')
	.factory('store', function ($http, $injector) {
		'use strict';
    return $injector.get('localStorage');
	})

	.factory('api', function ($resource) {
		'use strict';

		var store = {
			todos: [],

			api: $resource('/api/todos/:id', null,
				{
					update: { method:'PUT' }
				}
			),

			clearCompleted: function () {
				var originalTodos = store.todos.slice(0);

				var incompleteTodos = store.todos.filter(function (todo) {
					return !todo.completed;
				});

				angular.copy(incompleteTodos, store.todos);

				return store.api.delete(function () {
					}, function error() {
						angular.copy(originalTodos, store.todos);
					});
			},

			delete: function (todo) {
				var originalTodos = store.todos.slice(0);

				store.todos.splice(store.todos.indexOf(todo), 1);
				return store.api.delete({ id: todo.id },
					function () {
					}, function error() {
						angular.copy(originalTodos, store.todos);
					});
			},

			get: function () {
				return store.api.query(function (resp) {
					angular.copy(resp, store.todos);
				});
			},

			insert: function (todo) {
				var originalTodos = store.todos.slice(0);

				return store.api.save(todo,
					function success(resp) {
						todo.id = resp.id;
						store.todos.push(todo);
					}, function error() {
						angular.copy(originalTodos, store.todos);
					})
					.$promise;
			},

			put: function (todo) {
				return store.api.update({ id: todo.id }, todo)
					.$promise;
			}
		};

		return store;
	})

	.factory('localStorage', function ($q) {
		'use strict';

		var STORAGE_ID = 'todos-angularjs';

		var store = {
			todos: [],

			_getFromLocalStorage: function () {
				return JSON.parse(localStorage.getItem(STORAGE_ID) || '[]');
			},

			_saveToLocalStorage: function (todos) {
				localStorage.setItem(STORAGE_ID, JSON.stringify(todos));
			},

			clearCompleted: function () {
				var deferred = $q.defer();

				var incompleteTodos = store.todos.filter(function (todo) {
					return !todo.completed;
				});

				angular.copy(incompleteTodos, store.todos);

				store._saveToLocalStorage(store.todos);
				deferred.resolve(store.todos);

				return deferred.promise;
			},

			delete: function (todo) {
				var deferred = $q.defer();

				store.todos.splice(store.todos.indexOf(todo), 1);

				store._saveToLocalStorage(store.todos);
				deferred.resolve(store.todos);

				return deferred.promise;
			},

			get: function () {
				var deferred = $q.defer();

				angular.copy(store._getFromLocalStorage(), store.todos);
				deferred.resolve(store.todos);

				return deferred.promise;
			},

			insert: function (todo) {
				var deferred = $q.defer();

				store.todos.push(todo);

				store._saveToLocalStorage(store.todos);
				deferred.resolve(store.todos);

				return deferred.promise;
			},

			put: function (todo, index) {
				var deferred = $q.defer();

				store.todos[index] = todo;

				store._saveToLocalStorage(store.todos);
				deferred.resolve(store.todos);

				return deferred.promise;
			}
		};

		return store;
	});

(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .service('lastSignIn', lastSignIn);

  /** @ngInject */
  function lastSignIn($window) {
    var service = {
      isLikelyActive: isLikelyActive,
      signedIn: signedIn,
      signedOut: signedOut
    }
    return service;

    function signedIn(expiresAt) {
      $window.localStorage.signInExpiry = expiresAt;
    }

    function signedOut() {
      $window.localStorage.signInExpiry = 0;
    }

    function isLikelyActive() {
      var signInExpiry = $window.localStorage.signInExpiry;
      if (!signInExpiry) {
        return false;
      } else {
        var msLeft = parseInt(signInExpiry) - new Date().getTime();
        return msLeft > 0;
      }
    }
  }
})();

(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .service('auth', auth);

  /** @ngInject */
  function auth(GAuth, gapiScopes, gapiClientId, $state, $rootScope, lastSignIn) {
    GAuth.setConfig({
      clientId: gapiClientId,
      scope: gapiScopes,
      currentUserListener: currentUserChanged
    });

    var service = {
      checkAuth: checkAuth,
      signIn: signIn,
      signOut: signOut,
      signedIn: false,
      signingIn: false,
      userEmail: null,
      userName: null,
      token: null
    }
    return service;

    function currentUserChanged(user) {
      service.signingIn = false;
      service.signedIn = user.isSignedIn();
      if (service.signedIn) {
        service.userEmail = user.getBasicProfile().getEmail();
        service.userName = user.getBasicProfile().getName();
        service.token = user.getAuthResponse().access_token;
        lastSignIn.signedIn(user.getAuthResponse().expires_at);
      } else {
        service.userEmail = null;
        service.userName = null;
        service.token = null;
        lastSignIn.signedOut();
      }
      $rootScope.$digest();
    }

    function checkAuth() {
      GAuth.checkAuth();
    }

    function signIn() {
      service.signingIn = true;
      GAuth.signIn();
    }

    function signOut() {
      GAuth.signOut();
    }
  }
})();

(function() {
  'use strict';

  angular
  .module('wizweekPy')
  .service('api', api);

  /** @ngInject */
  function api(apiBaseUrl, auth, $http) {
    var service = {
      put: put,
      get: get,
      request: request
    };
    return service;

    function get(url) {
      return request('GET', url);
    }

    function put(url, data) {
      return request('PUT', url, data);
    }

    function request(method, url, data) {
      return $http({
        method: method,
        url: apiBaseUrl + url,
        data: data,
        headers: { 'Authorization': 'Bearer ' + auth.token }
      });
    }
  }
})();

